	module.exports = {"data":{"markdownRemark":{"html":"<h3>Directory Icons and Program Badges</h3>\n<p>The Directory page now includes pink and purple lightning icons to highlight programs that are:</p>\n<ul>\n<li>Fast to respond with a first response in &#x3C;48 hours on average</li>\n<li>Fast to award by giving a bounty &#x3C;14 days after submission</li>\n</ul>\n<p>We also include a <i>Managed</i> badge to identify programs that are managed by HackerOne. </p>\n<h3>Paying out Bounties via the API</h3>\n<p>Organizations now have the ability to payout and suggest bounties and swag using their internal systems via the API. You can view the API documentation for this <a href=\"https://api.hackerone.com/docs/v1#/reports/bounties\">here</a>.</p>\n<h3>Slack Integration 2.0</h3>\n<p>We've revamped our Slack integration so that programs can have:</p>\n<ul>\n<li>Granular notification filtering</li>\n<li>Support for multiple channels</li>\n<li>Notifications when a username is mentioned</li>\n</ul>\n<p>Read our <a href=\"https://www.hackerone.com/blog/slack-integration-update-2\">blog post</a> and learn how to <a href=\"/programs/slack-integration.html\">set up Slack integration</a>.</p>","frontmatter":{"path":"/changelog/2017/august","title":"August 2017"}}},"pathContext":{}}

